@import './variables';
@import url('https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@400;700&display=swap');
@import './button';

* {
  font-family: "Roboto Condensed", sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

ul,
li,
dl {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

a {
  text-decoration: none;
  color: inherit;
  display: inline-block;

  &:hover {
    text-decoration: none;
    color: inherit;
  }
}

p {
  margin: 0;
  padding: 0;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

body {
  background-color: #efebe7;
}

.app {
  width: 1100px;
  margin: 0 auto;
  padding: 0;
  position: relative;
  background-color: $white;
  box-shadow: 0 2px 120px $appShadow;

  .bg-decoration {
    position: absolute;
    right: -40px;
    bottom: 10px;
    z-index: 10;
    
    @media (max-width: 450px) {
      display: none;
    }
  }
}

main {
  position: relative;
  min-height: 100vh;
  padding-bottom: 15px;
}

.pulse {
  animation: pulse 1.5s ease-in-out .5s infinite
}

@keyframes pulse {
  0% {
    opacity: 1
  }
  50% {
    opacity: .4
  }
  100% {
    opacity: 1
  }
}
